.chat {
    width: 96%;
    height: 89vh;
    padding: 20px;
    // overflow: hidden;

    .option {
        display: flex;
        align-items: center;
        justify-content: right;
        margin-top: -20px;

        button {
            background-color: #7777f6;
            border: none;
            border-radius: 5px;
            color: white;
            // border: 2px solid white;
            width: 120px;
            height: 30px;
            margin: 10px;
            font-weight: bolder;
            font-size: 12px;
        }

        .actif {
            background-color: white;
            color: #7777f6;
            // border: 2px solid #7777f6;
        }
    }

    .bloc-message {
        width: 100%;
        height: 71vh;
        overflow: auto;

        .user-mess {
            display: flex;
            align-items: center;
            margin-bottom: 10px;

            .profil-mess {
                width: 40px;
                height: 40px;
                background-color: white;
                border-radius: 100%;
                margin-left: 10px;
            }

            .message {
                min-width: 200px;
                min-height: 30px;
                background-color: rgba(255, 255, 255, 0.6);
                padding: 10px;
                border-radius: 10px;
                margin-left: 10px;
                font-size: 12px;
                color: white;
                text-shadow: 1px 1px 0px black;

                span {
                    color: #7777f6;
                    font-size: 9px;
                    display: block;
                    font-weight: bolder;
                }

                .nom-user {
                    margin-top: -6px;
                }

                .heure {
                    margin-left: 85%;
                    // margin-top: 10px;
                }
            }
        }

        .my-mess {
            justify-content: right;
            margin-right: 10px;
        }
    }


    @keyframes envoyerAnim {
        from {
            height: 20px;
        }

        to {
            height: 45px;
        }
    }

    .bloc-saisie {
        display: flex;
        align-items: center;
        width: 100%;
        height: 70px;
        background-color: rgba(255, 255, 255, 0.4);
        border-radius: 10px;
        margin-top: 20px;
        // animation: envoyerAnim 0.5s;

        form {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 98%;
        }

        input {
            border: none;
            background-color: transparent;
            margin-left: 20px;
            width: 1000%;
            height: 50px;
            color: white;
        }

        input:hover {
            background-color: rgba(255, 255, 255, 0.1);
        }

        input:focus {
            outline: none;
        }




        button {
            background-color: #7777f6;
            width: 100px;
            height: 45px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 10px;
            cursor: pointer;
            border: none;
            margin-left: -10px;
            animation: envoyerAnim 0.5s;

            .ico-envoyer {
                width: 40px;
                height: 40px;
            }
        }

    }
}