
.acceuil{
    // height: 150vh;

    h3{
        color: #7777f6;
        position: relative;
        top: -10px;
        left: 10px;
    }

    .messages-dash{
        display: flex;
        padding: 20px;
        
        .presenveEtu{
            width: 63%;
            height: 52vh;
            margin-right: 15px;
            border: 2px solid rgba(255, 255, 255, 0.2);
            border-radius: 20px;

            .presenveEtu-content{
                height: 77%;
                // padding: 10px;
                // padding-left: 20px;
                // display: flex;
                // flex-direction: column;
                // margin-top: -20px;
                font-size: 8px;
                
            }

            span{
                color: white;
                margin-left: 90%;
                font-size: 14px;
                cursor: pointer;
            }
            
        }

        .participationEtu{
            width: 40%;
            height: 52vh;
            margin-right: 15px;
            border: 2px solid rgba(255, 255, 255, 0.2);
            border-radius: 20px;

            .participationEtu-content{
                height: 77%;
                margin-left: -25px;
                // padding: 10px;
                // padding-left: 20px;
                // display: flex;
                // flex-direction: column;
                // margin-top: -20px;
                font-size: 8px;
                
            }

            span{
                color: white;
                margin-left: 90%;
                font-size: 14px;
                cursor: pointer;
            }
            
        }

        .absence{
            width: 40%;
            height: 52vh;
            margin-right: 15px;
            border: 2px solid rgba(255, 255, 255, 0.2);
            border-radius: 20px;

            .absence-val{
                margin-top: -45px;
                margin-left: 86%;
            }

            .chart{
                margin-top: -30px;
            }

            .option{
                display: flex;
                font-size: 12px;
                margin-left: 20px;
                
                .title{
                    color: white;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .dot{
                        width: 10px;
                        height: 10px;
                        margin-right: 10px;
                        border: 1px solid white;
                    }
                }

                .value{
                    color: white;
                    margin-left: 5px;
                }
            }
            
        }
       
    }

    .info-dash{
        display: flex;
        padding: 20px;

       
        .recents{
            margin-top: -10px;
            width: 50%;
            height: 55vh;
            border: 2px solid rgba(255, 255, 255, 0.2);
            border-radius: 20px;
            margin-right: 20px;

            .recents-content{
                display: flex;
                padding-left: 20px;
                margin-left: 25px;

                .cours-recent{
                    font-size: 10px;
                    width: 200px;
                    height: 150px;
                    border: 1px solid rgba(255, 255, 255, 0.1);
                    border-radius: 10px;
                    background-color: rgba(0, 0, 0, 0.1);
                    position: relative;
                    margin: 12px;

                    h3{
                        // text-align: center;
                        margin-left: 10px;
                        font-size: 14px;
                        margin-top: 20px;
                        color: rgba(255, 255, 255, 0.6);
                    }

                    hr{
                        width: 100px;
                        margin-top: -18px;
                        margin-bottom: 10px;
                        border: 1px solid rgba(255, 255, 255, 0.2);
                    }

                    span{
                        color: rgba(255, 255, 255, 0.6);
                        margin-left: 20px;
                        font-size: 11px;
                    }

                    b{
                        display: block;
                        color: #7777f6;
                        margin-top: 20px;
                        margin-left: 62px;
                        font-size: 12px;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .participation-cr{
                            display: inline-block;
                            width: 8px;
                            height: 8px;
                            border: 2px solid white;
                            border-radius: 100%;
                            background-color: white;
                            margin-left: 4px;
                        }

                        .cr-red{
                            border: 2px solid white;
                            background-color: rgba(255, 0, 0, 0.6);
                        }
                    }

                    .link-cours{
                        color: #7777f6;
                    }
                }
            }
        }

        .annonce-etu{
            margin-top: -10px;
            width: 49%;
            height: 55vh;
            border: 2px solid rgba(255, 255, 255, 0.2);
            border-radius: 20px;

            .annonce-etu-val{
                margin-top: -45px;
                margin-left: 88%;
            }

            .annonce-content{
                
                    height: 77%;
                    padding: 10px;
                    padding-left: 20px;
                    display: flex;
                    flex-direction: column;
                    margin-top: -20px;
    
                    .annonce{
                        margin: 4px;
                        width: 85%;
                        height: 100px;
                        background-color: rgba(0, 0, 0, 0.1);
                        border-radius: 20px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
    
                        .profil-an{
                            width: 12%;
                            height: 60px;
                            background-color: white;
                            border-radius: 100%;
                            margin-left: 10px;
                            margin-right: 20px;
                        }
    
                        .info-an{
                            width: 75%;
                            h4{
                                color: #7777f6;
                                font-size: 12px;
                            }
    
                            p{
                                font-size: 10px;
                                color: rgba(255, 255, 255, 0.8);
                                margin-top: -15px;
                            }
    
                            b{
                                font-size: 12px;
                                color: #7777f6;
                                position: relative;
                                top: -10px;
                                left: 80%;
                            }
                        }
                    }
    
                    .an-2{
                        margin-left: 30px;
                    }
    
                    .an-3{
                        margin-left: 60px;
                    }

                }   
                span{
                    color: white;
                    margin-left: 90%;
                    font-size: 14px;
                    cursor: pointer;
                }
        }
    }
}